* {
  padding: 0px;
  margin: 0px;
  text-decoration: none;
  list-style: none;
}
body {
  font-family: $fonfFamily;
  color: $fontColorPrimary;
}
.container {
  // width: 100%;
  max-width: 320px;
  padding: 0 20px;
  margin: 0 auto;

  @include tablet {
    min-width: 768px;
    padding: 0 32px;
  }
  @include desktop {
    min-width: 1280px;
    padding: 0 32px;
  }
}
svg {
  vertical-align: middle;
}
